<template>
  <v-form
    ref="form"
    v-model="valid"
    lazy-validation
  >

    <!-- Name -->
    <v-text-field
      v-model="bookingName"
      :rules="nameRules"
      label="Name"
      required
    ></v-text-field>

    <!-- Email -->
    <v-text-field
      v-model="bookingEmail"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>

    <!-- Seminar Type Selection -->
    <v-select :items="seminars" v-model="seminarChoice" @change="getSlots(seminarChoice.id)" label="Seminar Type" required :rules="notNullRules">
      <template slot="item" slot-scope="data">
        {{ data.item.title }} ${{ data.item.price }}
      </template>
      <template slot="selection" slot-scope="data">
        {{ data.item.title }} ${{ data.item.price }}
      </template>
    </v-select>

    <!-- Seminar Slot Selection -->
    <v-select :items="slots" v-model="bookingChoice" label="Seminar Slot" required :rules="notNullRules">
      <template slot="item" slot-scope="data">
        {{ formatDate(data.item.start_datetime) }} - ({{ data.item.duration_in_mins }} mins)
      </template>
      <template slot="selection" slot-scope="data">
        {{ formatDate(data.item.start_datetime) }} - ({{ data.item.duration_in_mins }} mins)
      </template>
    </v-select>

    <!-- Validate Form -->
    <v-btn
      :disabled="!valid"
      color="success"
      class="mr-4"
      @click="validate"

    >
      Submit
    </v-btn>

    <!-- Reset Form -->
    <v-btn
      color="error"
      class="mr-4"
      @click="reset"
    >
      Reset Form
    </v-btn>

  </v-form>
</template>

<script>
import axios from "axios";
import moment from "moment";
import qs from "qs";

export default {
  data: () => ({
    valid: true,
    bookingName: null,
    bookingEmail: null,
    seminarChoice: null,
    bookingChoice: null,
    // validation rules
    nameRules: [
      v => !!v || 'Name is required',
    ],
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
    ],
    notNullRules: [
      v => !!v || 'Seminar choice required'
    ],
    seminars: [],
    slots: [],
  }),
  mounted() {
    this.page_load_data = document.body.getAttribute('data');
    this.seminars = JSON.parse(this.page_load_data)['seminars'];
  },
  methods: {
    postData() {
      axios.post('/book_class/signup/seminar', qs.stringify({
        student_name: this.bookingName,
        student_email: this.bookingEmail,
        slot_id: this.bookingChoice.id,
      }))
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    validate () {
        if (this.$refs.form.validate()) {
            this.snackbar = true;
            this.postData();
            this.$refs.form.reset();
        }
        this.snackbar = true;
        console.log(this.bookingChoice);
    },
    reset () {
      this.$refs.form.reset()
    },
    getSlots(id) {
      axios.get('/book_class/get/seminar_slots/' + id).then(response => {
        this.slots = response.data["slots"] || [];
      })
    },
    formatDate(datetimeStr) {
      var split_datetime = datetimeStr.split("T");
      var created_str = split_datetime[0] + " " + split_datetime[1];
      var dateObj = Date(datetimeStr);
     
      //return String(dateObj).format('MM/DD/YYYY hh:mm');
      return moment.format();
    }
  },
}
</script>